import React from 'react';
import './style.css';
import { useWidget } from '../store/chat.state';

const Widget = () => {
  // Your component logic goes here

  const widgetStore = useWidget((s) => s);

  return (
    <>
    <div id="botpenguin-launcher-12" className="botpenguin-right" style={{ visibility: 'visible', opacity: 1 }} onClick={()=>{
      widgetStore.toggle();
    }}>
  <div className="botpenguin-launcherImage-12">
    <div className="botpenguin-launcher-text-12">
      <span>👋 I'm here to help you.</span>
    </div>
    <span className="botpenguin-launcher-image-12" style={{ zIndex: 1, position: 'relative', background: 'rgb(18, 119, 253)' }}>
      <img src="https://cdn.botpenguin.com/assets/upload/iiBM-ob4oS4Tw.png" alt="" />
    </span>
  </div>
</div>
    </>
  );
};

export default Widget;