import { ThemeProvider, createTheme } from "@mui/material";
import "./App.css";
import ChatUI from "./chat";
import TestUI from "./test";
import Widget from "./widget";
import { useEffect } from "react";
import { useWidget } from "./store/chat.state";
import useHubspot from "./integrations/hubspot";

const theme = createTheme({
  palette: {
    primary: {
      main: "#1277fd", // Replace 'your_custom_color' with your desired color
    },
  },
});

function App() {

  const widgetStore = useWidget((s) => s);
  
  const hs = useHubspot();


  useEffect(()=>{
    console.log("app fully" , widgetStore.isFullyActive);
  },[widgetStore.isFullyActive])

  // useEffect(() => {
  //   // Load the HubSpot script
  //   const script = document.createElement("script");
  //   script.id = "hs-script-loader";
  //   script.type = "text/javascript";
  //   script.async = true;
  //   script.defer = true;
  //   script.src = "//js-eu1.hs-scripts.com/144211241.js";
  //   document.body.appendChild(script);

  //   return () => {
  //     // Clean up the script when the component unmounts
  //     document.body.removeChild(script);
  //   };
  // }, []);

  useEffect(() => {
    const loadScript = () => {
      const script = document.createElement("script");
      script.innerHTML = `
        (function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':
        new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],
        j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src=
        'https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);
        })(window,document,'script','dataLayer','GTM-KGGQC7J');
        
        function gtag(){dataLayer.push(arguments);}
      `;
      document.head.appendChild(script);
    };

    loadScript();

    // Clean up the script when the component unmounts
    return () => {
      // Assuming the script is the only dynamically injected script in the head
      const scripts = document.head.getElementsByTagName("script");
      for (let i = 0; i < scripts.length; i++) {
        if (scripts[i].innerHTML.includes("googletagmanager.com")) {
          document.head.removeChild(scripts[i]);
        }
      }
    };
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <div className="App" onClick={()=>{
        // console.log("App clicked");
        // hs.setTest(true)
        // hs.setIsHSOpen(true)
      }}>
        {/* <ChatUI/> */}

        {widgetStore.isFullyActive ? widgetStore.isActive ? <TestUI /> : <Widget /> : (null)}
      </div>
    </ThemeProvider>
  );
}

export default App;
