import useHubspot from "../integrations/hubspot";
import { useChat, useWidget } from "../store/chat.state";
import { fetchEventSource } from '@microsoft/fetch-event-source';
import Typing from "../utils/Typing";
import {
    Box,
    Button,
    Typography,
    Avatar,
    Paper,
  } from "@mui/material";

const Message = ({ message , index }) => {
  const isBot = message.sender === "bot";

  const messages = useChat((s) => s.messages);
  const chat = useChat((s) => s);

  const { isHSOpen, setIsHSOpen  } = useHubspot();
  const widgetStore = useWidget((s) => s);

  return (
    <div>
      <Box
        sx={{
          display: "flex",
          justifyContent: isBot ? "flex-start" : "flex-end",
          mb: 2,
        }}
      >
        <Box
          sx={{
            display: "flex",
            flexDirection: isBot ? "row" : "row-reverse",
            alignItems: "flex-start",
          }}
        >
          {isBot ? (
            <Avatar
              style={{
                border: "1px solid #1277fd",
              }}
              alt="Remy Sharp"
              src="https://cdn.botpenguin.com/assets/upload/iiBM-ob4oS4Tw.png"
            />
          ) : (
            <>
              {/* <Avatar sx={{ bgcolor: isBot ? "primary.main" : "secondary.main" }}>
                {isBot ? "B" : "U"}
              </Avatar> */}
            </>
          )}

          <Paper
            variant="outlined"
            sx={{
              py: 1,
              px: 2,
              ml: isBot ? 1 : 0,
              mr: isBot ? 0 : 1,
              border: "none",
              backgroundColor: isBot ? "rgb(245, 245, 245)" : "#1277fd",
              color: isBot ? "#000000" : "#ffffff",
              borderRadius: isBot ? "20px 20px 20px 2px" : "20px 20px 2px 20px",
              minHeight: "15px",
              minWidth: "40px",
            }}
          >
            <Typography variant="body1" sx={{ fontFamily: "Matter-Medium" }}>
              {message?.body ? message?.body : isBot ? <Typing /> : ""}
            </Typography>
          </Paper>
        </Box>
      </Box>
      {
        index === 0 && (
          <div style={{ marginLeft: "40px", marginBottom: "10px" }}>
        <Button
          className="action_btn"
          variant="outlined"
          color="primary"
          sx={{ borderRadius: 28, mt: 0.5, mx: 0.5, textTransform: "none" }}
          aria-label="add"
          onClick={() => {
            setIsHSOpen(!isHSOpen);

            //wARNING :  THIS CODE BLOCK DOESNT WORK HERE

            //widgetStore.fullyDeactive();
            //widgetStore.close();
          }}
        >
          <Typography variant="body1">Start LiveChat 🙎‍♂️</Typography>
        </Button>
      </div>
        )
      }
      {message?.chatButtons && (
        <div style={{ marginLeft: "40px", marginBottom: "10px" }}>
          {message.chatButtons.map((item) => (
            <Button
              className="action_btn"
              variant="outlined"
              color="primary"
              sx={{ borderRadius: 28, mt: 0.5, mx: 0.5, textTransform: "none" }}
              aria-label="add"
              onClick={async () => {
                chat.appendMessage({
                  id: messages?.length,
                  body: item,
                  sender: "user",
                });

                try {
                  let lastMessageIndex = messages?.length;

                  chat.appendMessage({
                    id: messages?.length,
                    body: "",
                    sender: "bot",
                  });

                  const response = await fetchEventSource(
                    `${process.env.REACT_APP_BACK_URL}/plotset/stream`,
                    {
                      method: "POST",
                      headers: {
                        "Content-Type": "text/event-stream",
                      },
                      body: JSON.stringify({
                        input: item,
                      }),
                      onmessage(msg) {
                        if (msg.event === "data") {
                          let messageData = JSON.parse(msg.data);
                          chat.streamChunkToLastMessage(messageData);
                        }
                      },
                      onerror(err) {
                        console.error("Error fetching data:", err);
                      },
                    }
                  );
                } catch (error) {
                  console.error("Error:", error);
                  chat.streamChunkToLastMessage(
                    "Sorry, I can't generate a response right now."
                  );
                }
              }}
            >
              <Typography variant="body1">{item}</Typography>
            </Button>
          ))}
        </div>
      )}
    </div>
  );
};

export default Message;
