import * as React from "react";
import "./style.css";
import {
  Box,
  TextField,
  Button,
  Typography,
  Avatar,
  Grid,
  Paper,
  AppBar,
  Toolbar,
  IconButton,
} from "@mui/material";
import SendIcon from "@mui/icons-material/Send";
import MenuIcon from "@mui/icons-material/Menu";
import MoreIcon from "@mui/icons-material/MoreVert";
import CloseIcon from "@mui/icons-material/Close";
import Tooltip from "@mui/material/Tooltip";
import MenuItem from "@mui/material/MenuItem";
import AdbIcon from "@mui/icons-material/Adb";
import Menu from "@mui/material/Menu";
import AttachFileIcon from "@mui/icons-material/AttachFile";
import Divider from "@mui/material/Divider";
import BoltIcon from "@mui/icons-material/Bolt";
import { useChat, useWidget } from "../store/chat.state";
import { fetchEventSource } from '@microsoft/fetch-event-source';
import Typing from "../utils/Typing";
import useHubspot from "../integrations/hubspot";
import Message from "./Message";

const TestUI = () => {
  const bottomRef = React.useRef(null);

  const [anchorElNav, setAnchorElNav] = React.useState(null);
  const [anchorElUser, setAnchorElUser] = React.useState(null);

  const messages = useChat((s) => s.messages);
  const chat = useChat((s) => s);
  const widgetStore = useWidget((s) => s);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  const settings = ["Profile", "Account", "Dashboard", "Logout"];

  const [input, setInput] = React.useState("");

  const handleSend = () => {
    if (input.trim() !== "") {
      setInput("");
    }
  };

  const handleInputChange = (event) => {
    setInput(event.target.value);
  };

  React.useEffect(() => {
    bottomRef?.current?.scrollIntoView();
  }, [messages]);

  return (
    <Box
      className="main_ui_container"
    >
      <AppBar
        position="static"
        sx={{
          borderTopRightRadius: "20px",
          borderTopLeftRadius: "20px",
          background: "#1277fd",
          zIndex: 2000000
        }}
      >
        <Toolbar>
          <IconButton onClick={() => {}} sx={{ p: 0 }}>
            <Avatar
              alt="Remy Sharp"
              src="https://cdn.botpenguin.com/assets/upload/iiBM-ob4oS4Tw.png"
            />
          </IconButton>
          <Typography
            variant="h6"
            component="div"
            sx={{ flexGrow: 1, ml: 2, fontFamily: "Matter-Medium" }}
          >
            BotPlotset
          </Typography>
          <Box sx={{ flexGrow: 0 }}>
            <Tooltip title="Open settings">
              <IconButton
                size="large"
                edge="end"
                color="inherit"
                aria-label="menu"
                onClick={handleOpenUserMenu}
                sx={{ mr: 0 }}
              >
                <MoreIcon />
              </IconButton>
              {/* <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                <Avatar alt="Remy Sharp" src="/static/images/avatar/2.jpg" />
              </IconButton> */}
            </Tooltip>
            <Menu
              sx={{ mt: "45px" }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              open={Boolean(anchorElUser)}
              onClose={handleCloseUserMenu}
            >
              {settings.map((setting) => (
                <MenuItem key={setting} onClick={handleCloseUserMenu}>
                  <Typography textAlign="center">{setting}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
          <IconButton
            size="large"
            edge="end"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 0 }}
            onClick={()=>{
              widgetStore.close();
            }}
          >
            <CloseIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <Box
        sx={{ flexGrow: 1, overflow: "auto", p: 2 }}
        className="custom_scrollbar"
      >
        {messages.map((message , index) => (
          <Message key={message.id} message={message} index={index} />
        ))}
        <div className="pt-2" ref={bottomRef} />
      </Box>
      <Divider sx={{ bgcolor: "primary.light" }} />
      <Box sx={{ p: 0.5, backgroundColor: "background.default" }}>
        <Grid container>
          <Grid item xs={10}>
            <TextField
              sx={{
                "& fieldset": { border: "none" },
              }}
              size="medium"
              fullWidth
              placeholder="Type a message"
              inputProps={{ style: { fontFamily: "Matter-Medium" } }} // font size of input text
              InputLabelProps={{ style: { fontFamily: "Matter-Medium" } }} // font size of input label
              variant="outlined"
              value={input}
              onChange={handleInputChange}
              onKeyDown={async (ev) => {
                if (ev.key !== "Enter" || !input) return;
                ev.preventDefault();
                setInput("");

                chat.appendMessage({ id: messages?.length, body: input, sender: "user" });

                //Start SSE
                // const eventSource = new EventSource('http://localhost:8000/plotset/stream');
                // eventSource.onmessage = (event) => {
                //   const eventData = JSON.parse(event.data);
                //   console.log('Received event:', eventData);
                //   // Update React state or perform other actions based on the received event
                // };

                //fetch

                // try {
                //   chat.appendMessage({ id: 5, body: " ", sender: "bot" });
                //   const response = await fetch(
                //     "http://localhost:8000/plotset/stream",
                //     {
                //       method: "POST",
                //       headers: {
                //         "Content-Type": "text/event-stream",
                //       },
                //       body: JSON.stringify({
                //         input: "what is plotset?",
                //       }),
                //     }
                //   );
                //   const dataStream = response.body;

                //   const reader = dataStream.getReader();
                //   while (true) {
                //     const { done, value } = await reader.read();
                //     if (done) break;
                //     // Handle the streamed data
                //     const originalStr = new TextDecoder().decode(value)?.toString();
                //     const secondLine = originalStr?.split("\n")?.[1];
                //     const onlyVal = secondLine?.replace("data:", "");
                //     console.log("Received data chunk:", onlyVal.replace(/"/g, ''));
                //     chat.streamChunkToLastMessage(onlyVal.replace(/"/g, ''))
                //   }
                // } catch (error) {
                //   console.error("Error fetching data:", error);
                // }

                try {
                  chat.appendMessage({ id: messages?.length, body: "", sender: "bot" });
                
                  const response = await fetchEventSource(`${process.env.REACT_APP_BACK_URL}/plotset/stream`, {
                    method: "POST",
                    headers: {
                      "Content-Type": "text/event-stream",
                    },
                    body: JSON.stringify({
                      input: input,
                    }),
                    onmessage (msg) {
                      if (msg.event === "data") {
                        let messageData = JSON.parse(msg.data)
                        chat.streamChunkToLastMessage(messageData);
                      }
                    },
                    onerror(err){
                      console.error("Error fetching data:",err);
                    },
                  });
                } catch (error) {
                  console.error("Error:", error);
                  chat.streamChunkToLastMessage("Sorry, I can't generate a response right now.");
                }
              }}
            />
          </Grid>
          <Grid item xs={1}>
            <IconButton
              size="large"
              edge="end"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 0, width: "100%" }}
            >
              <AttachFileIcon sx={{ color: "gray" }} />
            </IconButton>
          </Grid>
          <Grid item xs={1}>
            <IconButton
              size="large"
              edge="end"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 0, width: "100%" }}
            >
              <SendIcon color="primary" />
            </IconButton>
          </Grid>
        </Grid>
      </Box>
      <Box
        sx={{
          p: 0.5,
          backgroundColor: "background.default",
          borderBottomRightRadius: "20px",
          borderBottomLeftRadius: "20px",
        }}
        textAlign={"center"}
        justifyContent={"center"}
      >
        <Typography
          variant="body1"
          color={"primary"}
          textAlign={"middle"}
          sx={{ fontFamily: "Matter-Medium" }}
        >
          Chat <BoltIcon sx={{ verticalAlign: "middle", color: "#ffc107" }} />{" "}
          by BotPlotset
        </Typography>
      </Box>
    </Box>
  );
};

export default TestUI;
