//https://community.hubspot.com/t5/CMS-Development/Activating-Hubspot-Chat-via-Javascript/m-p/284077

import { useState, useEffect } from "react";
import { useWidget } from "../store/chat.state";

const useHubspot = () => {
  const [isHSOpen, setIsHSOpen] = useState(false);
  const [test, setTest] = useState(false);

  const widgetStore = useWidget((s) => s);

  const openHubspot = () => {
    if (window.HubSpotConversations) {
      window.HubSpotConversations.widget.load();
      window.HubSpotConversations.widget.open();
    }
  };

  // const handleHubspotClose = (message) => {
  //   if (!message.origin.includes("hubspot")) return;

  //   let messageData =
  //     typeof message.data === typeof ""
  //       ? JSON.parse(message.data)
  //       : message.data;

  //   if (messageData?.type !== "open-change") return;

  //   let { isOpen, isUser } = messageData?.type?.data;

  //   setIsHSOpen(isOpen);
  // };

  useEffect(() => {
    const handleMessage = (message) => {
      if (!message.origin.includes("hubspot")) return;
      console.log({ message });

      let messageData =
        typeof message.data === typeof ""
          ? JSON.parse(message.data)
          : message.data;
      console.log(messageData);

      if (messageData?.type === "request-widget") {
        console.log("hubspot loaded!");
        if (widgetStore.isFullyActive) {
          window.HubSpotConversations.widget.remove();
        }
      }

      if (messageData?.type === "open-change") {
        let { isOpen, isUser } = messageData?.data;

        if(!isOpen){
          window.HubSpotConversations.widget.remove();
          widgetStore.fullyActive(true);
        }
        // setIsHSOpen(isOpen);
      }
    };

    window.addEventListener("message", handleMessage);

    return () => {
      window.removeEventListener("message", handleMessage);
    };
  }, [widgetStore.isFullyActive]);

  useEffect(() => {
    console.log("isOpen changed:", isHSOpen); // This will log the updated value of isOpen

    if (isHSOpen) {
      console.log("Hubspot opened!");
      openHubspot(); // Open Hubspot after state update
      widgetStore.fullyDeactive();
      widgetStore.close();
    }
    // else {
    //   widgetStore.fullyActive();
    // }
  }, [isHSOpen, test, widgetStore.isFullyActive]); // This effect will run whenever isOpen changes

  return { isHSOpen, openHubspot, setIsHSOpen, test, setTest };
};

export default useHubspot;
